<template>
    <li class="cursor-pointer one_wid" style="cursor: pointer !important;" :style="{ borderColor: initialColor + ' !important' }">
        <template v-for="(item, index) in NumberWidgetData">
            <h3 :style="{ color: initialColor + ' !important' }">{{ item.VALUE }}<br><span>{{ item.NAME }}</span></h3>
            <i aria-hidden="true" class="fa fa fa-list-ul"></i>
            <i :class="{'d-none': !isInEditMode,}" @click="removeItem()" aria-hidden="true" class="fa fa-times"></i>
        </template>
    </li>
</template>
<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";
export default {
    props: {
        reportName: null,
        chartType: String, // Set the type
        dashboardid: Number,
        moduleCode: null,
        fromDate: "",
        toDate: "",
        index: null,
        reportId: null,
        isSystemReport: null,
        numberLayout: null,
        isInEditMode: null,
    },
    data() {
        return {
        isLoading: true,
        action: this.reportName,
        charT_TYPE: "numbers_chart",
        xaxis: "",
        yaxis: "",
        NumberWidgetData: [],
        selectedColors: ["#23a5fd", "#37b8df", "#c49e71", "#23a5fd", "#dbb429"],
        selectedBackgroundColors: ["#edf7ff", "#eef7f9", "#fff4e8", "#edf7ff", "#fff8df"],
        initialColor: '',
        initialBackgroundColor: '',
        colorCounter: 0,
        showAddDashboardReport: false,
        chart: null, // Add a chart property to store the C3.js chart instance
        };
    },
    created() {
        this.FetchData();
        this.initialColor = this.getRandomColor();
        this.initialBackgroundColor = this.getRandomBackgroundColor();
    },
    methods: {
        removeItem() {
            var vm = this;
            this.$parent.removeItem(vm.i, vm.reportId, vm.chartType);
        },
        getRandomColor() {
            const randomIndex = Math.floor(Math.random() * this.selectedColors.length);
            return this.selectedColors[randomIndex];
        },
        getRandomBackgroundColor() {
            const randomIndex = Math.floor(Math.random() * this.selectedBackgroundColors.length);
            return this.selectedBackgroundColors[randomIndex];
        },
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
            var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=`;
            await DataService.GetDashboardData(params).then(function (response) {
                console.log(response);
                if (response.data.result != null && response.data.result != "") {
                    if (response.data.status == "Success") {
                        if (response.data.result.data != null) {
                            vueObj.NumberWidgetData = response.data.result.data;
                            console.log(vueObj.NumberWidgetData);
                            vueObj.xaxis = response.data.result.Xaxis;
                            vueObj.yaxis = response.data.result.Yaxis;
                        }
                    }
                }
                vueObj.isLoading = false;
            });
        },
    },
};
</script>